// client/middleware.ts
import { NextResponse } from "next/server"
import type { NextRequest } from "next/server"
import jwt from "jsonwebtoken"

const JWT_SECRET = process.env.JWT_SECRET || "supersecret"

export function middleware(req: NextRequest) {
  const token = req.cookies.get("token")?.value
  const { pathname } = req.nextUrl

  // Case 1: No token â†’ force login
  if (!token) {
    if (pathname !== "/login") {
      return NextResponse.redirect(new URL("/login", req.url))
    }
    return NextResponse.next()
  }

  try {
    jwt.verify(token, JWT_SECRET) // validate

    // Case 2: Already logged in but trying to go back to /login
    if (pathname === "/login") {
      return NextResponse.redirect(new URL("/", req.url)) // ðŸ‘ˆ redirect to home
    }

    return NextResponse.next()
  } catch {
    // Case 3: Invalid/expired token
    return NextResponse.redirect(new URL("/login", req.url))
  }
}

export const config = {
  matcher: ["/((?!_next/static|_next/image|favicon.ico|api).*)"],
}
